import { useState } from "react";

const FormModal = ({ isOpen, onClose, activeTab }) => {
  const [modalOpen, setModalOpen] = useState(false);
  const [modalMessage, setModalMessage] = useState("");
  const [errors, setErrors] = useState({});

  // Google Form action URL
  const googleFormAction =
    "https://docs.google.com/forms/u/0/d/e/1FAIpQLSdx-qrrSiMnzAhTbAaxU7UJfj0aZJlyPgCl3smjVFaWh4k-xA/formResponse";

  // Field IDs from the Google Form
  const fieldNames = {
    name: "entry.271377800",
    mobile: "entry.1059613645",
    email: "entry.1361867851",
    studentTeacher: "entry.2132025188",
    locality: "entry.505168970",
    classGrade: "entry.2115027460",
  };

  // Form validation function
  const validateForm = (formData) => {
    const newErrors = {};
    const name = formData.get(fieldNames.name);
    if (!name || !/^[a-zA-Z\s]+$/.test(name)) {
      newErrors[fieldNames.name] = "Enter a valid name (letters and spaces only)";
    }
    const mobile = formData.get(fieldNames.mobile);
    if (!mobile || !/^[6-9]\d{9}$/.test(mobile)) {
      newErrors[fieldNames.mobile] = "Enter a valid 10-digit Indian number (6-9 start)";
    }
    if (activeTab === "tutor") {
      const email = formData.get(fieldNames.email);
      if (!email || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
        newErrors[fieldNames.email] = "Enter a valid email address";
      }
    }
    if (activeTab === "student") {
      const classGrade = formData.get(fieldNames.classGrade);
      if (!classGrade) {
        newErrors[fieldNames.classGrade] = "Class/Grade is required";
      }
    }
    const locality = formData.get(fieldNames.locality);
    if (!locality) {
      newErrors[fieldNames.locality] = "Locality is required";
    }
    return newErrors;
  };

  // Form submission handler
  const handleSubmit = async (e) => {
    e.preventDefault();
    const form = e.target;
    const formData = new FormData(form);
    const validationErrors = validateForm(formData);
    setErrors(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      setModalMessage("Please fix the errors in the form.");
      setModalOpen(true);
      return;
    }

    try {
      await fetch(googleFormAction, {
        method: "POST",
        body: formData,
        mode: "no-cors",
      });
      setModalMessage("Form submitted successfully!");
      setModalOpen(true);
      form.reset();
      setErrors({});
      onClose(); // Close the form modal on success
    } catch (error) {
      console.error("Error submitting form:", error);
      setModalMessage("There was an error submitting the form. Please try again.");
      setModalOpen(true);
    }
  };

  // Feedback Modal inside FormModal
  const FeedbackModal = ({ isOpen, message, onClose }) => {
    if (!isOpen) return null;
    return (
      <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
        <div className="bg-white p-6 rounded-lg shadow-lg">
          <p className="text-lg">{message}</p>
          <button
            onClick={onClose}
            className="mt-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          >
            Close
          </button>
        </div>
      </div>
    );
  };

  if (!isOpen) return null;

  return (
    <>
      <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
        <div className="bg-gray-200 p-6 rounded-lg shadow-lg w-full max-w-md mx-4">
          <h2 className="text-xl font-bold mb-4">
            {activeTab === "student" ? "Book a Tutor" : "Register as Tutor"}
          </h2>
          <form onSubmit={handleSubmit} className="space-y-4">
            <input
              type="hidden"
              name={fieldNames.studentTeacher}
              value={activeTab === "student" ? "Student" : "Tutor"}
            />
            <div>
              <input
                type="text"
                name={fieldNames.name}
                placeholder="Enter your name"
                className="w-full px-4 py-2 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
              {errors[fieldNames.name] && (
                <p className="text-red-500 text-sm mt-1">{errors[fieldNames.name]}</p>
              )}
            </div>
            <div>
              <input
                type="text"
                name={fieldNames.mobile}
                placeholder="+91 Enter mobile number"
                className="w-full px-4 py-2 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
              {errors[fieldNames.mobile] && (
                <p className="text-red-500 text-sm mt-1">{errors[fieldNames.mobile]}</p>
              )}
            </div>
            <div>
              <input
                type="text"
                name={fieldNames.locality}
                placeholder="Enter your locality"
                className="w-full px-4 py-2 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
              {errors[fieldNames.locality] && (
                <p className="text-red-500 text-sm mt-1">{errors[fieldNames.locality]}</p>
              )}
            </div>
            {activeTab === "student" ? (
              <div>
                <input
                  type="text"
                  name={fieldNames.classGrade}
                  placeholder="Enter Class/Grade"
                  className="w-full px-4 py-2 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
                {errors[fieldNames.classGrade] && (
                  <p className="text-red-500 text-sm mt-1">{errors[fieldNames.classGrade]}</p>
                )}
              </div>
            ) : (
              <div>
                <input
                  type="email"
                  name={fieldNames.email}
                  placeholder="Enter your email"
                  className="w-full px-4 py-2 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
                {errors[fieldNames.email] && (
                  <p className="text-red-500 text-sm mt-1">{errors[fieldNames.email]}</p>
                )}
              </div>
            )}
            <div className="flex justify-between">
              <button
                type="submit"
                className="px-6 py-2 bg-blue-500 text-white rounded-3xl hover:bg-blue-600"
              >
                Submit
              </button>
              <button
                type="button"
                onClick={onClose}
                className="px-6 py-2 bg-gray-500 text-white rounded-3xl hover:bg-gray-600"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      </div>
      <FeedbackModal
        isOpen={modalOpen}
        message={modalMessage}
        onClose={() => setModalOpen(false)}
      />
    </>
  );
};

export default FormModal;