// import { useState } from 'react';
import { useNavigate } from "react-router-dom"; // Import useNavigate for URL changes
import styled from "styled-components";

const Navbar = ({ activeTab }) => {
  const navigate = useNavigate(); // Hook to programmatically navigate

  const handleTabChange = (tab) => {
    navigate(`/${tab}`); // Update URL and let Router handle the rest
  };

  return (
    <StyledNav className="fixed top-0 left-0 right-0 text-black p-4 z-50 flex justify-between items-center mt-8 md:mt-6 mx-4 md:mx-10">
      <div className="text-2xl font-bold text-gray-500">Tutor Kendra</div>

      <div className="items-center gap-4 flex flex-row flex-nowrap">
        <span className="text-xl font-bold items-center gap-2 bg-sky-100 rounded-3xl pr-3 hidden lg:flex">
          <div className="bg-green-700 rounded-full py-[3px] px-[4px]">
            <img
              width="35"
              height="35"
              src="https://img.icons8.com/ios/100/ffffff/phone.png" // Light/white icon
              alt="phone"
            />
          </div>
          +916388204164
        </span>
        <StyledWrapper>
          <div id="tabFilter" className="filter-switch">
            <input
              checked={activeTab === "student"}
              id="student"
              name="tabs"
              type="radio"
              onChange={() => handleTabChange("student")}
            />
            <label className="option" htmlFor="student">
              Student
            </label>
            <input
              checked={activeTab === "tutor"}
              id="tutor"
              name="tabs"
              type="radio"
              onChange={() => handleTabChange("tutor")}
            />
            <label className="option" htmlFor="tutor">
              Tutor
            </label>
            <span className="background" />
          </div>
        </StyledWrapper>
      </div>
    </StyledNav>
  );
};

const StyledNav = styled.nav`
  background: rgba(0, 0, 0, 0); /* Semi-transparent black for contrast */
  backdrop-filter: blur(10px); /* Blur effect */
  -webkit-backdrop-filter: blur(10px); /* Safari support */
  border-radius: 15px; /* Optional: rounded corners */
`;

const StyledWrapper = styled.div`
  .filter-switch {
    border: 2px solid #3674b5;
    border-radius: 30px;
    position: relative;
    display: flex;
    align-items: center;
    height: 50px;
    width: 200px;
    overflow: hidden;
  }
  .filter-switch input {
    display: none;
  }
  .filter-switch label {
    flex: 1;
    text-align: center;
    cursor: pointer;
    border: none;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: all 0.5s;
    font-weight: 500;
    font-size: 16px;
  }
  .filter-switch .background {
    position: absolute;
    width: 49%;
    height: 38px;
    background-color: #3674b5;
    top: 4px;
    left: 4px;
    border-radius: 30px;
    transition: left 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  #tutor:checked ~ .background {
    left: 50%;
  }
  #student:checked + label[for="student"] {
    color: #ffffff;
    font-weight: bold;
  }
  #tutor:checked + label[for="tutor"] {
    color: #ffffff;
    font-weight: bold;
  }
  #student:not(:checked) + label[for="student"],
  #tutor:not(:checked) + label[for="tutor"] {
    color: #7d7d7d;
  }

  /* Responsive adjustments */
  @media (max-width: 640px) {
    .filter-switch {
      width: 150px;
      height: 40px;
    }
    .filter-switch label {
      font-size: 14px;
    }
    .filter-switch .background {
      height: 28px;
    }
  }
`;

export default Navbar;
