import { cn } from '../utils/cn';

const Lights = ({ className }) => (
  <div className={cn('w-full h-full overflow-hidden relative', className)}>
    {/* Layer 1: Initial gradient */}
    <div
      className="w-full h-full absolute top-0 left-0 layer-1"
      style={{
        background: 'conic-gradient(from 180deg at 50% 50%, var(--blue-500) 0deg, var(--cyan-400) 120deg, var(--yellow-400) 240deg, var(--purple-500) 360deg)',
        filter: 'blur(75px)',
        opacity: '20%',
        animation: 'fadeLayer1 10s infinite ease-in-out'
      }}
    />
    {/* Layer 2: Transitioning gradient */}
    <div
      className="w-full h-full absolute top-0 left-0 layer-2"
      style={{
        background: 'conic-gradient(from 180deg at 50% 50%, var(--purple-500) 0deg, var(--pink-400) 120deg, var(--blue-500) 240deg, var(--cyan-400) 360deg)',
        filter: 'blur(75px)',
        opacity: '0%',
        animation: 'fadeLayer2 10s infinite ease-in-out'
      }}
    />
    {/* Layer 3: Next gradient */}
    <div
      className="w-full h-full absolute top-0 left-0 layer-3"
      style={{
        background: 'conic-gradient(from 180deg at 50% 50%, var(--cyan-400) 0deg, var(--yellow-400) 120deg, var(--purple-500) 240deg, var(--pink-400) 360deg)',
        filter: 'blur(75px)',
        opacity: '0%',
        animation: 'fadeLayer3 10s infinite ease-in-out'
      }}
    />
    {/* Layer 4: Final gradient */}
    <div
      className="w-full h-full absolute top-0 left-0 layer-4"
      style={{
        background: 'conic-gradient(from 180deg at 50% 50%, var(--yellow-400) 0deg, var(--blue-500) 120deg, var(--pink-400) 240deg, var(--purple-500) 360deg)',
        filter: 'blur(75px)',
        opacity: '0%',
        animation: 'fadeLayer4 10s infinite ease-in-out'
      }}
    />
  </div>
);

// Inject keyframes into the document
const styleSheet = document.createElement('style');
styleSheet.textContent = `
  @keyframes fadeLayer1 {
    0% { opacity: 20%; }
    25% { opacity: 0%; }
    50% { opacity: 0%; }
    75% { opacity: 0%; }
    100% { opacity: 20%; }
  }
  @keyframes fadeLayer2 {
    0% { opacity: 0%; }
    25% { opacity: 20%; }
    50% { opacity: 0%; }
    75% { opacity: 0%; }
    100% { opacity: 0%; }
  }
  @keyframes fadeLayer3 {
    0% { opacity: 0%; }
    25% { opacity: 0%; }
    50% { opacity: 20%; }
    75% { opacity: 0%; }
    100% { opacity: 0%; }
  }
  @keyframes fadeLayer4 {
    0% { opacity: 0%; }
    25% { opacity: 0%; }
    50% { opacity: 0%; }
    75% { opacity: 20%; }
    100% { opacity: 0%; }
  }
`;
document.head.appendChild(styleSheet);

export default Lights;